.todolistmain {
  position: relative;
}
.todolistmain .hearder input {
  padding: 10px;
  font-size: 12px;
  border: 2px solid #fff;
  width: 300px;
  border-radius: 2px;
  margin-right: 5px;
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

.todolistmain .hearder {
  text-align: center;
}

.todolistmain .hearder .heading h1 {
  color: rgb(238, 238, 238);
  font-size: 30px;
  padding-bottom: 50px;
}

.todolistmain .hearder input:focus {
  outline: none;
  border-color: rgb(0, 174, 255);
}

.todolistmain .hearder button {
  padding: 10px;
  border: none;
  font-size: 12px;
  border-radius: 2px;
  background: rgb(0, 174, 255);
  color: white;
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.todolistmain .hearder button:hover {
  background: rgb(22, 139, 194);
}

.todolistmain .thelist {
  position: absolute;
}
.todolistmain .thelist li {
  width: 350px;
  padding: 10px;
  background: rgba(248, 248, 248, 0.932);
  margin-top: 10px;
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  list-style: none;
  -webkit-transition: background-color 0.2s ease-out;
  -o-transition: background-color 0.2s ease-out;
  transition: background-color 0.2s ease-out;
  font-size: 12px;
}

.todolistmain .thelist li:hover {
  background: lightsalmon;
  cursor: pointer;
}



